.report {
  .signature-display {
    width: 50%;
  }

  .signature-image {
    display: block;
  }

  .photos {
    margin-top: 35px;
    img {
      width: 100%;
    }
  }

  #page {
    max-width: none;
  }

  .offer-breakdown--header .score {
    line-height: 44px;
  }

  .offer-breakdown--report .col:first-child ul li:before {
    content: "";
  }

  .offer-breakdown--report ul li:before {
    display: none;
  }

  & main > .row {
    margin-top: 20px;
  }

  .form-assessmentTitle {
    margin-top: 10px;
    margin-bottom: 15px;
    font-size: 26px;
    text-align: center;
  }

  @include at-query($min,$large ) {
    .row > .row > .col {
      &:first-child {
        padding-left: 0;
      }
      &:last-child {
        padding-right: 0;
      }
    }
  }
}

.form-isCarBought {
  .alert {
    text-align: center;
    background: #e2e2e2;
    border-radius: 4px;
    margin-bottom: 22px;
  }

  div:nth-last-of-type(2) {
    margin-bottom: 33px;
    margin-top: 0;
  }

  #purchasePrice {
    margin-bottom: 10px;
  }
}

.report-detail--wrap {
  background: white;
  padding: 10px;
}

.report-detail {
  margin-bottom: 10px;

  label {
    font-size: 1em;
    display: block;
  }

  p {
    margin-top: 0;

    &:empty {
      display: none;
    }
  }
}
