.purchaseForm {
  display: block;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
    margin-bottom: 10px;
}

#purchaseModal {

  .input-field {
    text-align: left;
  }

  .input-fieldLast {
    margin-top: 0px;
    margin-bottom: 30px;
  }
}

.signature-display {
  display: block;
  border : none;
  border-bottom: 1px solid $medium-grey;
  border-radius: 0;
  outline: none;
  height: 3rem;
  width: 100%;
}

.signature-image {
  padding: 0.4rem 0;
  display: none;
  height: 100%;
}

.close-btn {
  cursor: pointer;
}
